;AACF;;AAEA,sCAAsC;;AAEtC;EACE,YAAY;EACZ,aAAa;EACb,2CAA2C;EAC3C,gBAAgB;EAChB,kBAAkB;;EAElB,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,aAAa;AACf","sourcesContent":[":root {\n  --green: #34a853;\n  --yellow: #ffcc00;\n  --red: #f36464;\n  --light-purple: #eaddff;\n  --border-line: #e0e0e0;\n  --focused-yellow: #ffcc00;\n  --light-yellow: #ffefc0;\n  --medium-purple: #d0bcff;\n  --dark-purple: #4b387a;\n  --light-blue: #d3e3ff;\n  --bright-blue: #299dfd;\n  --smoke-white: #dbdbdb;\n  --btn-disabled: #a8a8a8;\n  --btn-disabled-light: #d5d5d5;\n  --btn-disabled-text: #7a7a7a;\n  --turquoise: rgb(85, 140, 121);\n  --dark-grey: #343434;\n  --grey-bg-containers: #f4f4f4;\n  --items-display-bg: #f7f7f7;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n  font-family:\n    \"Lucida Sans\", \"Lucida Sans Regular\", \"Lucida Grande\",\n    \"Lucida Sans Unicode\", Geneva, Verdana, sans-serif;\n}\n\nbutton,\ninput,\nselect {\n  background-color: transparent;\n  border: none;\n  margin: 0;\n  padding: 0;\n  text-align: inherit;\n  font: inherit;\n  border-radius: 0;\n  appearance: none;\n  cursor: pointer;\n  outline: none;\n}\n\nbutton.inputButton:disabled {\n  background-color: var(--btn-disabled-light);\n  color: var(--btn-disabled-text);\n  cursor: unset;\n}\n\nbutton.active-yellow {\n  background-color: var(--focused-yellow);\n}\n\ninput {\n  cursor: text;\n  margin-left: 15px;\n  width: 250px;\n}\n\ninput[type=\"checkbox\"] {\n  max-width: 20px;\n  height: 20px;\n  accent-color: green;\n  border: 2px solid var(--dark-grey);\n  border-radius: 2px;\n}\ninput[type=\"checkbox\"]:hover {\n  cursor: pointer;\n}\ninput[type=\"checkbox\"]:checked {\n  background-color: var(--btn-disabled);\n  background-image: url(\"./svg/check.svg\");\n  background-position: center;\n  border: none;\n}\n\nlabel {\n  min-width: 120px;\n}\n\ninput:read-only {\n  cursor: unset;\n  width: 250px;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\ninput[type=\"date\"] {\n  width: 100%;\n  display: flex;\n  position: relative;\n  padding: 10px 90px 10px 0px;\n}\n\ninput[type=\"date\"]::after {\n  content: \"Pick Date\";\n  position: absolute;\n  right: 15px;\n  top: 6px;\n  color: var(--dark-purple);\n  pointer-events: none;\n}\n\ninput[type=\"date\"]::-webkit-calendar-picker-indicator {\n  background-color: var(--light-purple);\n  position: absolute;\n  top: 0px;\n  right: 0px;\n  padding: 8px 90px 8px 13px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 8px;\n  cursor: pointer;\n}\n\n.inputField {\n  text-align: left;\n}\n\n#transparent-backdrop {\n  width: 100vw;\n  height: 100vh;\n  background-color: white;\n  opacity: 0.7;\n  position: fixed;\n  inset: 0;\n  z-index: 1;\n}\n\n@font-face {\n  font-family: \"handWritten_Bold\";\n  src:\n    url(\"./fonts/Lemonade Bold.woff2\") format(\"woff2\"),\n    url(\"./fonts/Lemonade Bold.woff\") format(\"woff\");\n  font-weight: 400;\n  font-style: normal;\n  font-stretch: normal;\n  unicode-range: U+0020-007A;\n}\n\n@font-face {\n  font-family: \"handWritten_Reg\";\n  src:\n    url(\"./fonts/Lemonade Regular.woff2\") format(\"woff2\"),\n    url(\"./fonts/Lemonade Regular.woff\") format(\"woff\");\n  font-weight: 400;\n  font-style: normal;\n  font-stretch: normal;\n  unicode-range: U+0020-007A;\n}\n\n@font-face {\n  font-family: \"handWritten_Thin\";\n  src:\n    url(\"./fonts/Lemonade Thin.woff2\") format(\"woff2\"),\n    url(\"./fonts/Lemonade Thin.woff\") format(\"woff\");\n  font-weight: 400;\n  font-style: normal;\n  font-stretch: normal;\n  unicode-range: U+0020-007A;\n}\n\n/* .eventsDisabled {\n    pointer-events: 0;\n    opacity: 0.5;\n} */\n\n.push-right {\n  margin-left: auto;\n}\n\n/* main elements */\n\n#appContainer {\n  display: flex;\n}\n\n#leftContainer {\n  padding: 30px;\n  width: 400px;\n  height: 100vh;\n  border-right: 1px solid var(--border-line);\n  box-sizing: border-box;\n}\n\n#rightContainer {\n  width: 1